import React from 'react';
import { HashRouter, Route } from 'react-router-dom';
import Home from './routes/Home';
import Navigation from './components/Navigation';
/* import ArtistTab from './routes/ArtistTab'; */
import Artist from './routes/Artist';
import ArtistDetail from './routes/ArtistDetail';
import Audition from './routes/Audition';
import Apply from './routes/Apply';
import FAQ from './routes/FAQ';


const App = () => {
  return (
    <div>
      <HashRouter>
        <Navigation />
        <Route path='/' exact={true} component={Home} />
        {/* <Route path='/artistTab' exact={true} component={ArtistTab} /> */}
        <Route path='/artist' exact={true} component={Artist} />
        <Route path='/artist/detail' component={ArtistDetail} />
        <Route path='/audition' exact={true} component={Audition} />
        <Route path='/audition/apply' component={Apply} />
        <Route path='/audition/faq' component={FAQ} />
      </HashRouter>
    </div>
  );
};

export default App;