import React from 'react';
import {Link} from 'react-router-dom';
import './AuditionSection.scss'

const AuditionSection = () => {
    return (
        <div className='auditionWrap'>
            <article className='left row'>
                <h4>Audition</h4>
                <ul>
                    <li><Link to='/audition' className='lnk'>지원안내</Link></li>
                    <li><Link to='/audition/apply' className='lnk'>지원하기</Link></li>
                    <li><Link to='/audition/faq' className='lnk'>FAQ</Link></li>
                </ul>
            </article>
            <airticle className='right row'>
                <h4>지원안내 <span>Ι Audition info</span></h4>
                <div className='info'>
                    <div className='imgbox'>
                        <img src='images/cravity_al_1.jpg' alt='오디션 정보' />
                        <h3>STARSHIP AUDITION </h3>
                    </div>
                    <h5><img src='images/web_icon.png' alt='' />모집 분야</h5>
                    <p>보컬 · 랩 · 댄스 · 외모</p>
                    <h5><img src='images/web_icon.png' alt='' />모집 절차분야</h5>
                    <p>
                        홈페이지 및 우편 접수 → 1차 합격자 개별 공지 → 카메라 테스트 및 담당자 오디션 → 최종 오디션<br />
                        *우편 접수 주소 : 06070 서울시 강남구 삼성로146길 4-5 스타쉽ENT 신인개발팀 앞
                    </p>
                    <h5><img src='images/web_icon.png' alt='' />지원 방법</h5>
                    <p>
                    · 하단의 ‘오디션 지원하기’ 버튼 클릭 후 지원서 작성<br />
                    · 필수 첨부 파일<br />
                    　　1) 정면 클로즈업 및 전신사진 파일 총 2장<br />
                    　　2) 지원 분야에 따른 영상 및 음성 파일<br />
                    · 보컬/랩 - 자유곡, 1분 이상의 영상 또는 음성 파일<br />
                    · 댄스 - 자유곡, 1분 이상의 영상 파일<br />
                    · 외모 - 자유 형식의 30초 이상의 영상 파일 (연기, 자기소개, 워킹, 포즈 등)
                    </p>
                    <h5><img src='images/web_icon.png' alt='' />유의사항</h5>
                    <p>
                    · 포토샵 &amp; 필터 등으로 수정 처리 혹은 정면이 아닌 사진은 1차 심사 대상에서 제외됩니다.<br />
                    · 포커스가 맞지 않은 동영상 파일, 음질이 불량한 음성 및 동영상 파일은 심사에 불이익이 있을 수 있습니다.
                    </p>
                    <div className='btn'>
                        <button><Link to='/audition/apply' className='lnk'>지원하기</Link></button>
                    </div>
                </div>
            </airticle>
        </div>

    );
};

export default AuditionSection;