import React, { useState, useEffect} from 'react';
import cn from 'classnames';
import {Link} from 'react-router-dom';
import './Header.scss'

const Header = () => {

    const [opener, setOpener] = useState(false);
    const openclose = () => {
        setOpener(!opener)
    }
    const [on,setOn] = useState(false);



    return (
        <div className='headerWrap'>
            <div className='row'>
                <h1 className='logo'>
                    <Link to='/' className='lnk'><img src="http://www.starship-ent.com/img/logo.png" alt="스타쉽엔터테인먼트" title="" /></Link>
                </h1>
                <nav className='nav'>
                    <ul className='depth1'>
                        <li onMouseEnter={() => setOn(true)} onMouseLeave={() => setOn(false)} className='list'>
                            COMPANY
                            <ul className={cn('depth2', {on})}>
                                <li><a href='#'>회사소개</a></li>
                                <li><a href='#'>대표인사말</a></li>
                                <li><a href='#'>연혁</a></li>
                                <li><a href='#'>사업현황</a></li>
                                <li><a href='#'>오시는 길</a></li>
                            </ul>
                        </li>
                        <li onMouseEnter={() => setOn(true)} onMouseLeave={() => setOn(false)} className='list'>
                            STARS
                            <ul className={cn('depth2', {on})}>
                                <li><Link to='/artist' className='lnk'>아티스트</Link></li>
                                <li><a href='#'>배우</a></li>
                                <li><a href='#'>엔터테이너</a></li>
                            </ul>
                        </li>
                        <li onMouseEnter={() => setOn(true)} onMouseLeave={() => setOn(false)} className='list'>
                            PR CENTER
                            <ul className={cn('depth2', {on})}>
                                <li><a href='#'>NOTICE</a></li>
                                <li><a href='#'>MULTIMEDIA</a></li>
                                <li><a href='#'>NEW RELEASE</a></li>
                            </ul>
                        </li>
                        <li onMouseEnter={() => setOn(true)} onMouseLeave={() => setOn(false)} className='list'>
                            AUDITION
                            <ul className={cn('depth2', {on})}>
                                <li><Link to='/audition' className='lnk'>지원안내</Link></li>
                                <li><Link to='/audition/apply' className='lnk'>지원하기</Link></li>
                                <li><Link to='/audition/faq' className='lnk'>FAQ</Link></li>
                            </ul>
                        </li>
                    </ul>
                </nav>
                <div className='lang' onClick={openclose}>
                    <button className={cn('lang', {on})}>한국어</button>·
                    <button className={cn('lang', {on})}>ENGLISH</button>·
                    <button className={cn('lang', {on})}>中文</button>·
                    <button className={cn('lang', {on})}>日本語</button>
                </div>
            </div>
        </div>
    );
};

export default Header;