import React from 'react';
import {Link} from 'react-router-dom';
import './AuditionSection.scss'

const FAQsec = () => {
    return (
        <div className='auditionWrap'>
            <article className='left row'>
                <h4>Audition</h4>
                <ul>
                    <li><Link to='/audition' className='lnk'>지원안내</Link></li>
                    <li><Link to='/audition/apply' className='lnk'>지원하기</Link></li>
                    <li><Link to='/audition/faq' className='lnk'>FAQ</Link></li>
                </ul>
            </article>
            <airticle className='right row'>
                <h4>FAQ <span>Ι 오디션 질문</span></h4>
                <div className='info'>
                    <div className='imgbox'>
                        <img src='images/cravity_al_1.jpg' alt='오디션 정보' />
                        <h3>AUDITION FAQ</h3>
                    </div>
                    <h5><img src='images/web_icon.png' alt='' />합격 연락은 언제 오나요?</h5>
                    <p>합격자에 한하여 2~3주 내로 개별 공지됩니다.</p>
                    <h5><img src='images/web_icon.png' alt='' />팀 지원이 가능한가요?</h5>
                    <p>
                        듀엣이나 팀 지원은 받지 않습니다.<br />
                        팀으로 지원하셔도 심사 과정에서 탈락 처리됨으로 개별 지원해주시기 바랍니다. 
                    </p>
                    <h5><img src='images/web_icon.png' alt='' />기존 오디션에 지원했었는데, 재지원이 가능한가요?</h5>
                    <p>
                        네, 가능합니다. 단, 같은 영상과 사진으로 재 지원하실 경우는 심사를 할 수 없으니 최근 사진과 영상으로 준비하여 지원 부탁드립니다.
                    </p>
                    <h5><img src='images/web_icon.png' alt='' />해외 자원자도 접수 가능한가요?</h5>
                    <p>
                        네, 가능합니다.<br />
                        국내 지원자와 동일한 방식으로 자료 첨부하여 홈페이지 혹은 우편으로 지원해주시면 됩니다. 
                    </p>
                    <h5><img src='images/web_icon.png' alt='' />다음 공개 오디션 계획이 있나요?</h5>
                    <p>
                        아직 상세 일정은 미정이오나, 이번 공개 오디션에 보내주신 뜨거운 성원에 힘입어 다시 한번 여러분을 만날 수 있도록 계획 중에 있으니 많은 기대 부탁드립니다. 다음 공개 오디션 전까지 홈페이지 및 우편 오디션 상시 지원은 가능합니다.
                    </p>
                    <h5><img src='images/web_icon.png' alt='' />만 14세 미만은 오디션 지원이 불가한가요?</h5>
                    <p>
                        만 14세 미만 지원자의 경우 아쉽게도 홈페이지 및 우편 오디션 지원은 불가합니다. 
                    </p>
                </div>
            </airticle>
        </div>
    );
};

export default FAQsec;