import React from 'react';
import {Link} from 'react-router-dom';
import './ApplySec.scss';
import styled from 'styled-components';


const StyledInput = styled.input`
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #888;
    width: 100%;
    margin: 6px 0;
`;

const ButtonInput = styled.input`
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #888;
    text-align: center;
    background: #eee;
    cursor: pointer;
`;

const FileInput = styled.input`
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #888;
    text-align: center;
    cursor: pointer;
    width: 100%;
    margin: 6px 0;
`;

const ApplySec = () => {
    return (
        <div className='applyWrap'>
            <article className='left row'>
                <h4>Audition</h4>
                <ul>
                    <li><Link to='/audition' className='lnk'>지원안내</Link></li>
                    <li><Link to='/audition/apply' className='lnk'>지원하기</Link></li>
                    <li>FAQ</li>
                </ul>
            </article>
            <article className='right'>
                <h4>지원하기 <span>Ι Audition apply</span></h4>
                <div className='form'>
                    <h5>기본 정보 <span>수정이 불가하니 신중히 입력해주세요</span></h5>
                    <form>
                        <StyledInput name='title' placeholder="'오디션 지원합니다'로 입력해주세요" type='text' className='w60' />
                        <ButtonInput type='button' name='gender' value='남' className='w20 l2' />
                        <ButtonInput type='button' name='gender' value='여' className='w20 l2' />
                        <ButtonInput type='button' name='part' value='보컬' className='w15 r1' />
                        <ButtonInput type='button' name='part' value='랩' className='w15 r1 l1' />
                        <ButtonInput type='button' name='part' value='댄스' className='w15 r1 l1' />
                        <ButtonInput type='button' name='part' value='외모' className='w15 l1' />
                        <StyledInput name='nation' placeholder="국적" type='text' className='w20 l2' />
                        <StyledInput name='body' placeholder="신장/체중" type='text' className='w20 l2' />
                        <StyledInput name='name' placeholder="이름" type='text' className='w50 r1' />
                        <StyledInput name='birth' placeholder="생년월일 (예: 950101)" type='text' className='w50 l1' />
                        <StyledInput name='phone' placeholder="휴대폰번호" type='text' className='w50 r1' />
                        <StyledInput name='job' placeholder="학교/직업" type='text' className='w50 l1' />
                        <StyledInput name='address' placeholder="주소" type='text' />
                        <StyledInput name='mail' placeholder="E-mail" type='text' className='w50 r1' />
                        <StyledInput name='sns' placeholder="SNS ID" type='text' className='w50 l1' />
                        <StyledInput name='talent' placeholder="특기/경력사항" type='text' />
                        <StyledInput name='url1' placeholder="동영상 URL 01" type='text' />
                        <StyledInput name='url2' placeholder="동영상 URL 02" type='text' />
                        <p className='ex'>※ 동영상 파일은 URL 주소를 입력하세요</p>
                        <FileInput name='file' label='' type='file' />
                        <FileInput name='file' label='' type='file' />
                        <FileInput name='file' label='' type='file' />
                    </form>
                    <h5>개인정보 수집 및 이용에 대한 안내</h5>
                    <textarea name="contract" id="contract" cols="" rows="" readonly="readonly">
                        Starship Entertainment’(이하 ‘회사’)는 지원자의 개인정보를 중요시하며, “정보통신망 이용촉진 및 정보보호”에 관한 법률을 준수하고 있습니다. 회사는 개인정보취급방침을 통하여 지원자께서 제공하시는 개인정보가 어떠한 용도와 방식으로 이용되고 있으며, 개인정보보호를 위해 어떠한 조치가 취해지고 있는지 알려드립니다. 회사는 개인정보취급방침을 개정하는 경우 웹사이트 공지사항(또는 개별공지)을 통하여 공지할 것입니다.

                        [수집하는 개인정보 항목]
                        회사는 원활한 지원자 상담서비스 제공을 위해 아래와 같은 개인정보를 수집하고 있습니다.
                        수집항목 : 이름, 성별, 생년월일, 휴대폰번호, 이메일
                    </textarea>
                </div>
                <div className='btn'>
                    <button><Link to='/audition' className='lnk'>지원하기</Link></button>
                </div>
            </article>
        </div>
    );
};

export default ApplySec;