import React from 'react';
import {Link} from 'react-router-dom';
import './Footer.scss'

const Footer = () => {
    return (
        <div className='footerWrap'>
            <div className='row'>
                <h1 className='logo'>
                    <Link to='/' className='lnk'><img src="images/logo_bt.png" alt="스타쉽엔터테인먼트" title="" /></Link>
                </h1>
                <div className='info'>
                    <p>
                        서울시 강남구 삼성로146길 4-5 스타쉽ENT(06070)<br />
                        4-5, Samseong-ro 146-gil, Gangnam-gu, Seoul<br />
                        <br />
                        스타쉽ENT의 모든 컨텐츠는 저작권의 보호를 받고 있습니다.<br />
                        Copyright © STARSHIP ENT all rights Reserved.
                    </p>
                </div>
                <div className='sns'>
                    <h6>STARSHIP</h6>
                    <div className='icon'>
                        <img src='images/icon_1.png' alt='V LIVE' />
                        <img src='images/icon_2.png' alt='naver TV' />
                        <img src='images/icon_3.png' alt='Facebook' />
                        <img src='images/icon_4.png' alt='Twitter' />
                        <img src='images/icon_5.png' alt='Youtube' />
                        <img src='images/icon_6.png' alt='Instagram' />
                        <img src='images/icon_7.png' alt='naver POST' />
                    </div>
                    <h6>KINGKONG by STARSHIP</h6>
                    <div className='icon'>
                        <img src='images/icon_1.png' alt='V LIVE' />
                        <img src='images/icon_2.png' alt='naver TV' />
                        <img src='images/icon_3.png' alt='Facebook' />
                        <img src='images/icon_8.png' alt='Weibo' />
                        <img src='images/icon_5.png' alt='Youtube' />
                        <img src='images/icon_6.png' alt='Instagram' />
                        <img src='images/icon_7.png' alt='naver POST' />
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Footer;